body{
  background-color: black;
}
#textblock {
    background-color: #210002;
    height: 100vh;
  }
  
  #textblock-container {
    width: 50%;
    margin: 0 auto;
    padding-top: 70px;
  }
  
  #textblock-title {
    color: #ffaf1b;
    font-size: 35px;
    font-weight: 600;
    font-family: "Helvetica Neue";
  }
  
  #textblock-content {
    color: #ffaf1b;
    font-size: 20px;
  }
  
  #textblock-footer {
    color: #ffaf1b;
    font-size: 15px;
    font-weight: 400;
    position: fixed;
    width: 100%;
    bottom: 0px;
    justify-content:center;
    align-items:center;
    text-align:center;
    margin-bottom: 20px;
  
  }
  
  #textblock-devsense {
    text-decoration: none;
    color: #ffaf1b;
    font-size: 15px;
    font-weight: 600;
  }
  
  .animation, .animation_layer {
      height: 200vh;
  }
  
  .animation {
    display: block;
      position: relative;
      z-index: 10;
  }
  
  .animation_layer {
      background-position: bottom center;
      background-size: auto 1038px;
      background-repeat: repeat-x;
      width: 100%;
      position: absolute;
  }
  .animation_layer.parallax {
      position: fixed;
  }
  
  #artback {
    background-image: url(./images/mbrapa.png);
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  #mountain {
    background-image: url(./images/guri.png);
    background-repeat: no-repeat;
    background-size: cover;

  }
  
  #jungle1 {
    background-image: url(./images/francesco-tommasini-7oIRGbmkbc0-unsplash\ \(1\).png);
    background-repeat: no-repeat;
    background-size: cover;

  }
  
